import React, { useState, useContext } from 'react'
import { Col, Row } from 'antd'
import GateMenu from './components/GateMenu'
import Circuit from './components/Circuit'
import Edit from './components/Edite'
import { gateTypeContext } from './Context'
import { nanoid } from 'nanoid'
import styles from './index.module.scss'

export default function Qcomposer() {
  let defaultCircuit = [
    [
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
    ],
    [
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
    ],
    [
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
    ],
    [
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
      null,
    ],
  ]
  const [circuitData, setCircuit] = useState(defaultCircuit)
  const [geteType, setGeteType] = useState({})
  const [coordinate, setcoordinate] = useState({})

  // 获取拖拽位释放gate位置的轴坐标
  const getDragCoordinate = (row, column) => {
    // setcoordinate(Coordinate)
    setcoordinate({ row, column })
  }
  const onDragEnd = () => {
    let circuitGateOne = {}
    let circuitGateTow = {}
    let circuitGateThree = {}
    console.log(coordinate, 'coordinate')
    // 释放拖拽过来的门
    if (geteType) {
      switch (geteType.bit) {
        // 当拖拽过来的门为1bit时
        case 1:
          circuitGateOne = {
            id: nanoid(),
            name: geteType.name,
            row: coordinate.row,
            column: coordinate.column,
            isconnector: true,
            bit: 1,
            ranking: 0,
          }
          // 判断当前位置是否有门
          // 当前位置没有门时
          if (circuitData[coordinate.column][coordinate.row] == null) {
            circuitData[coordinate.column][coordinate.row] = circuitGateOne
          } else {
            // 当前位置有门时
            switch (circuitData[coordinate.column][coordinate.row].bit) {
              // 当前位置门为1bit时
              case 1:
                circuitData[coordinate.column][coordinate.row] = circuitGateOne
                break
              // 当前位置门为2bit时 再判断当前位置门处于第几个
              case 2:
                // 判断当前是2bit门的第几个
                switch (
                  circuitData[coordinate.column][coordinate.row].ranking
                ) {
                  // 第一个时
                  case 0:
                    circuitData[coordinate.column][coordinate.row] =
                      circuitGateOne
                    circuitData[coordinate.column + 1][coordinate.row] = null
                    break
                  // 第二个时
                  case 1:
                    circuitData[coordinate.column][coordinate.row] =
                      circuitGateOne
                    circuitData[coordinate.column - 1][coordinate.row] = null
                    break
                  default:
                }
                break
              // 放置的位置是3bit的门时 再判断当前位置门处于第几个
              case 3:
                switch (
                  circuitData[coordinate.column][coordinate.row].ranking
                ) {
                  // 第一个时
                  case 0:
                    circuitData[coordinate.column][coordinate.row] =
                      circuitGateOne
                    circuitData[coordinate.column + 1][coordinate.row] = null
                    circuitData[coordinate.column + 2][coordinate.row] = null
                    break
                  // 第二个时
                  case 1:
                    circuitData[coordinate.column - 1][coordinate.row] = null

                    circuitData[coordinate.column][coordinate.row] =
                      circuitGateOne
                    circuitData[coordinate.column + 1][coordinate.row] = null

                    break
                  case 2:
                    circuitData[coordinate.column - 2][coordinate.row] = null

                    circuitData[coordinate.column - 1][coordinate.row] = null

                    circuitData[coordinate.column][coordinate.row] =
                      circuitGateOne
                    break
                  default:
                }
                break
              default:
            }
          }

          break
        // 拖拽过来的门是2比特时
        case 2:
          let id = nanoid()
          circuitGateOne = {
            id,
            name: geteType.name,
            row: coordinate.row,
            column: coordinate.column,
            isconnector: false,
            connector: 0,
            bit: 2,
            ranking: 0,
          }

          circuitGateTow = {
            id,
            name: geteType.name,
            row: coordinate.row,
            column: coordinate.column,
            isconnector: true,
            connector: coordinate.column - 1,
            bit: 2,
            ranking: 1,
          }

          switch (true) {
            case circuitData[coordinate.column][coordinate.row]?.bit === 3 &&
              circuitData[coordinate.column][coordinate.row]?.ranking === 0:
              circuitData[coordinate.column + 2][coordinate.row] = null

              break
            case circuitData[coordinate.column + 1][coordinate.row]?.bit ===
              3 &&
              circuitData[coordinate.column + 1][coordinate.row]?.ranking === 0:
              circuitData[coordinate.column + 2][coordinate.row] = null
              circuitData[coordinate.column + 3][coordinate.row] = null

              break
            case circuitData[coordinate.column + 1][coordinate.row]?.bit ===
              2 &&
              circuitData[coordinate.column][coordinate.row]?.ranking === 1:
              circuitData[coordinate.column + 2][coordinate.row] = null
              circuitData[coordinate.column - 1][coordinate.row] = null
              break
            case circuitData[coordinate.column][coordinate.row]?.ranking === 1:
              circuitData[coordinate.column - 1][coordinate.row] = null
              break
            case circuitData[coordinate.column][coordinate.row]?.ranking === 2:
              circuitData[coordinate.column - 1][coordinate.row] = null
              circuitData[coordinate.column - 2][coordinate.row] = null
              break
            case circuitData[coordinate.column + 1][coordinate.row]?.bit === 2:
              circuitData[coordinate.column + 2][coordinate.row] = null
              break

            default:
          }
          circuitData[coordinate.column][coordinate.row] = circuitGateOne
          circuitData[coordinate.column + 1][coordinate.row] = circuitGateTow
          break
        case 3:
          let idThree = nanoid()
          circuitGateOne = {
            id: idThree,
            name: geteType.name,
            row: coordinate.row,
            column: coordinate.column,
            isconnector: false,
            connector: 0,
            bit: 3,
            ranking: 0,
          }

          circuitGateTow = {
            id: idThree,
            name: geteType.name,
            row: coordinate.row,
            column: coordinate.column,
            isconnector: false,
            connector: coordinate.column - 1,
            bit: 3,
            ranking: 1,
          }
          circuitGateThree = {
            id: idThree,
            name: geteType.name,
            row: coordinate.row,
            column: coordinate.column,
            isconnector: true,
            connector: coordinate.column - 2,
            bit: 3,
            ranking: 2,
          }
          switch (true) {
            case circuitData[coordinate.column][coordinate.row]?.ranking === 1:
              circuitData[coordinate.column - 1][coordinate.row] = null
              break
            case circuitData[coordinate.column + 1][coordinate.row]?.ranking ===
              0 &&
              circuitData[coordinate.column + 1][coordinate.row]?.bit === 3:
              circuitData[coordinate.column + 3][coordinate.row] = null
              break
            default:
          }
          circuitData[coordinate.column][coordinate.row] = circuitGateOne
          circuitData[coordinate.column + 1][coordinate.row] = circuitGateTow
          circuitData[coordinate.column + 2][coordinate.row] = circuitGateThree
          break
        default:
      }
    }
  }
  console.log(circuitData)
  return (
    <div className={styles.root}>
      <div className="container">
        <Row>
          <Col span={6}>
            <div className="edit">
              <Edit />
            </div>
          </Col>
          <Col span={18}>
            <div className="main">
              <div className="gateMenu">
                <gateTypeContext.Provider value={{ geteType, setGeteType }}>
                  <GateMenu />
                </gateTypeContext.Provider>
              </div>
              <div onMouseUp={onDragEnd} className="circuit">
                <Circuit
                  getDragCoordinate={(row, column) =>
                    getDragCoordinate(row, column)
                  }
                  circuitData={circuitData}
                />
              </div>
            </div>
          </Col>
        </Row>
      </div>
    </div>
  )
}
